.header__container {
  max-width: 1400px;
  width: 85%;
  margin: 0 auto;
}

.header {
  width: 100%;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: #fff;
  height: 100px;
  display: flex;
  align-items: center;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    width: 233px;
    img {
      width: 100%;
    }
    @media (max-width: 767px) {
      width: 170px;
    }
    @media (max-width: 567px) {
      width: 130px;
    }
    @media (max-width: 400px) {
      width: 120px;
    }
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
    @media (max-width: 567px) {
      gap: 20px;
    }
    @media (max-width: 400px) {
      gap: 10px;
    }
  }
}

.header__lang {
  width: 70px;
  height: 40px;
  display: flex;
  label {
    cursor: pointer;
    color: black;
    margin: 0 auto;
  }
  @media (max-width: 400px) {
    width: 65px;
  }
}
.sl-nav {
  display: inline;
}
.sl-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  display: inline-block;
}
.sl-nav li {
  cursor: pointer;
  font-size: 24px;
  line-height: 30px;
  font-weight: 400;
}
.sl-nav li ul {
  display: none;
}
.sl-nav li:hover ul {
  position: absolute;
  top: 30px;
  right: -65px;
  display: block;
  background: #fff;
  width: 90px;
  padding-top: 0px;
  z-index: 1;
  border-radius: 5px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
}
.sl-nav li:hover .triangle {
  position: absolute;
  top: 16px;
  right: -70px;
  z-index: 10;
  height: 14px;
  overflow: hidden;
  width: 30px;
  background: transparent;
}
.sl-nav li:hover .triangle:after {
  content: "";
  display: block;
  z-index: 20;
  width: 15px;
  transform: rotate(45deg) translateY(0px) translatex(10px);
  height: 15px;
  background: #fff;
  border-radius: 2px 0px 0px 0px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}
.sl-nav li ul li {
  position: relative;
  text-align: left;
  background: transparent;
  padding: 15px 15px;
  padding-bottom: 0;
  z-index: 2;
  font-size: 15px;
  color: #3c3c3c;
}
.sl-nav li ul li:last-of-type {
  padding-bottom: 15px;
}
.sl-nav li ul li span {
  padding-left: 5px;
}
.sl-nav li ul li span:hover,
.sl-nav li ul li span.active {
  color: #146c78;
}
.sl-flag {
  display: inline-block;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.4);
  width: 15px;
  height: 15px;
  background: #aaa;
  border-radius: 50%;
  position: relative;
  top: 2px;
  overflow: hidden;
}
