.header__search {
  margin-top: 3px;
  cursor: pointer;
}
.header__burger {
  font-size: 32px;
  cursor: pointer;
  display: none;

  @media (max-width: 1499px) {
    display: block;
  }
}

#nav-icon1 {
  width: 27px;
  height: 20px;
  position: relative;
  margin-left: 23px;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;

  @media (max-width: 567px) {
    // margin-left: 10px;
  }
  @media (max-width: 400px) {
    margin-left: 15px;
  }
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: #616161;
  border-radius: 7px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0px;
}

#nav-icon1 span:nth-child(2) {
  top: 9px;
}

#nav-icon1 span:nth-child(3) {
  top: 18px;
}

#nav-icon1.open span:nth-child(1) {
  top: 9px;
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

#nav-icon1.open span:nth-child(3) {
  top: 9px;
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
